// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'; // Import Swiper styles

import 'swiper/css';
import "swiper/css/pagination";
import "swiper/css/navigation"; // import required modules

import SwiperCore, { Autoplay, Pagination, Navigation } from 'swiper';
SwiperCore.use([Autoplay, Navigation, Pagination]);
import { ref, computed } from 'vue';
import { useStore } from 'vuex';
import { encode } from 'js-base64';
import { useRouter } from 'vue-router';
import mapComponent from "@/components/mapComponent";
export default {
  name: "businessProfile",
  props: ['info', 'identity', 'canEdit', 'fromDeal'],
  components: {
    Swiper,
    SwiperSlide,
    mapComponent
  },

  setup() {
    let thumbsSwiper = ref(null);

    const setThumbsSwiper = swiper => {
      thumbsSwiper.value = swiper;
    };

    const store = useStore();
    const router = useRouter();

    const setNowChatUserInfo = data => store.commit('nowChatUserInfo', data);

    const setShowChatStatus = () => store.commit('showChatStatus', true);

    function turnChatPage() {
      router.push({
        path: '/chat/messages'
      });
    }

    const currentUser = computed(() => store.state.currentUser);
    return {
      thumbsSwiper,
      setThumbsSwiper,
      setNowChatUserInfo,
      setShowChatStatus,
      currentUser,
      turnChatPage
    };
  },

  methods: {
    exitPreview() {
      this.$router.push('/account/home');
    },

    backToSearchResults() {
      this.$emit('back');
    },

    editProfile() {
      let companyId = this.info.id;
      let identity = this.identity;
      let str = '';

      if (companyId) {
        let strObj = {
          cid: companyId,
          action: 'edit'
        };
        str = encode(JSON.stringify(strObj));
      } else {
        let strObj = {
          i: identity,
          action: 'add'
        };
        str = encode(JSON.stringify(strObj));
      }

      if (identity == 2) {
        this.$router.push({
          path: '/business/edit/recruiter',
          query: {
            s: str
          }
        });
      }

      if (identity == 3) {
        this.$router.push({
          path: '/business/edit/school',
          query: {
            s: str
          }
        });
      }

      if (identity == 4) {
        this.$router.push({
          path: '/business/edit/other',
          query: {
            s: str
          }
        });
      }

      if (identity == 5) {
        this.$router.push({
          path: '/vendor/edit/vendor',
          query: {
            s: str
          }
        });
      }
    },

    chat(userId) {
      let user = this.currentUser;

      if (this.goEasy.getConnectionStatus() === 'disconnected') {
        this.service.connect(user);
      }

      console.log(userId);
      let token = localStorage.getItem('token');

      if (!token || token === '') {
        return this.$router.push('/edupassport');
      }

      let companyInfo = this.info;
      let type = this.GoEasy.IM_SCENE.PRIVATE;
      let name = companyInfo.display_name;
      let avatar = companyInfo.logo ? companyInfo.logo : 'https://oss.esl-passport.cn/educator.png';
      let nowUserInfo = {
        uuid: userId,
        name: name,
        avatar: avatar,
        identity: this.identity
      };
      let textMessage = this.goEasy.im.createTextMessage({
        text: 'Hello',
        to: {
          id: userId,
          type: type,
          data: {
            name: name,
            avatar: avatar,
            identity: this.identity
          }
        }
      });
      let localHistory;

      if (type === this.GoEasy.IM_SCENE.PRIVATE) {
        localHistory = this.service.getPrivateMessages(userId);
      } else {
        localHistory = this.service.getGroupMessages(userId);
      } // console.log(localHistory)


      localHistory.push(textMessage);
      this.goEasy.im.sendMessage({
        message: textMessage,
        onSuccess: function (message) {
          console.log("发送成功.", message);
        },
        onFailed: function (error) {
          console.log("发送失败:", error);
        }
      });
      this.setNowChatUserInfo(nowUserInfo); // this.setShowChatStatus()

      this.turnChatPage();
    }

  }
};